<template>
	<div class="navigation">
		<el-container style="height: 700px; border: 1px solid #eee">
			<el-container>
				<el-main>
					<el-table :data="musicList" stripe>
						<el-table-column type="index" label="序号" width="90"></el-table-column>
						<el-table-column width="40px">
							<template slot-scope="scope">
								<i  @click="playMusic(musicList[scope.$index])"></i>
							</template>
						</el-table-column>
						<el-table-column prop="name" label="歌曲" width="240">
						</el-table-column>
						<el-table-column prop="artists[0].name" label="歌手" width="220">
						</el-table-column>
						<el-table-column prop="album.name" label="所属专辑">
						</el-table-column>
						<el-table-column label="操作">
							<template slot-scope="scope">
								<div class="video_img">
									<span id="span1">
										<i id="playmusic" class="el-icon-caret-right"
											@click="playMusic(musicList[scope.$index].id)"></i>
									</span>
								</div>
							</template>
						</el-table-column>
					</el-table>
				</el-main>
				<el-footer>
					<audio :src="musicUrl" style="width: 100%;" controls="controls" autoplay="autoplay" @play="play"
						@pause="pause">
						当前浏览器不支持audio播放
					</audio>
				</el-footer>
			</el-container>
		</el-container>
	</div>
</template>

<script>
	import axios from 'axios'
	
	export default {
		data() {
			
			return {
				//歌曲url
				musicUrl: "",
				musicList:this.$store.state.mulist,//取值
				//评论
				commentList: [],
				//是否播放
				isPlaying: false,
				//歌曲封面图片
				musicCover: "",
				//mv地址
				mvUrl: "",
			}
		},
		methods: {
			/* 获取歌曲链接 */
			playMusic(id) {
				var that = this;
				//console.log(id);
				axios.get("https://autumnfish.cn/song/url?id=" + id)
					.then(function(response) {
						//console.log(response.data.data[0].url);
						that.musicUrl = response.data.data[0].url;
					}, function(err) {})
				const h = this.$createElement;
				this.$message({
					type: "success",
					message: "歌曲已开始播放^_^"
				});
			},
			play() {
				this.isPlaying = true;
			},
			pause() {
				this.isPlaying = false;
			},
		},
	}
</script>

<style>
	/*导航栏样式*/
	.el-header {
		background-color: #AA0000;
		color: #333;
		line-height: 60px;
	}

	#musicDemo {
  width: 40px;
  height: 40px;
  /* margin-top: 5px; */
  margin-left: 20px;
transform: translateY(8px);
}
	#spanTitle {
		width: 100px;
		margin-left: 10px;
		margin-top: 15px;
		font-weight: 500;
		font-size: 20px;
		color: #ffffff;
		transform: translateX(-700px);

	}

	.el-main {
		height: 100%;
	}

	/*搜索样式*/
	.my-autocomplete {
		li {
			line-height: normal;
			padding: 7px;

			.name {
				text-overflow: ellipsis;
				overflow: hidden;
			}

			.addr {
				font-size: 12px;
				color: #b4b4b4;
			}

			.highlighted .addr {
				color: #ddd;
			}
		}
	}

	#playmusic {
		width: 10px;
		height: 10px;
		color: #c0c5c8;
		transform: translate(-10px, -5px);
		cursor: pointer;
	}

	#playmusic:hover {
		color: black;
	}

	#playmv {
		width: 10px;
		height: 10px;
		color: #FF8888;
		transform: translate(-10px, -5px);
		cursor: pointer;
	}
	
	#span1 {
		display: inline-block;
		width: 40px;
		height: 40px;
		line-height: 50px;
		font-size: 30px;
		border: 1px solid #c0c5c8;
		color: #fff;
		margin: 0 auto;
		/*水平居中*/
		position: relative;
		top: 50%;
		/*偏移*/
		/* transform: translateY(-50%); */
		border-radius: 50%;
		text-align: center;
		cursor: pointer;
	}
	#span1:hover {
		border:1px solid #000000;
	}
	.el-icon-setting{
		color: aliceblue;
		font-size: 40px;
		margin-top:10px ;
	}
	.el-icon-setting:hover{
		cursor: pointer;
		color: skyblue;
	}
</style>
